import { Row, Col, Card, List } from "antd";
import { withTranslation } from "react-i18next";
import { Slide } from "react-awesome-reveal";
import { MiddleBlockSection } from "./styles";
import { SvgIcon } from "../../common/SvgIcon";
interface MiddleBlockProps {
  title: string;
  content: string;
  button: string;
  t: any;
}

const MiddleBlock = ({ title, content, button, t }: MiddleBlockProps) => {
  const usecaseList = [
    {
      title: "Construction",
      svg: "construction.svg",
      des: "Our custom bag solutions are rigorously tested for all your sand, gravel, and aggregrate needs.",
    },
    {
      title: "Mining",
      svg: "mining.svg",
      des: "No matter the material youâ€™re mining, our bags are built to last in storage and transport.",
    },
    {
      title: "Agriculture",
      svg: "agriculture.svg",
      des: "Food safe storage and transport for your agriculture needs such as corn and grain.",
    },
    {
      title: "Chemicals",
      svg: "chemicals.svg",
      des: "Dangerous chemicals can pose many issues. Our experts are here to make sure all of your needs are met.",
    },
    {
      title: "Resin",
      svg: "resin.svg",
      des: "High quality solutions for transporting and storing resin materials is something we are keenly aware of.",
    },
    {
      title: "Food",
      svg: "food.svg",
      des: "Hygenic bulk bags designed for food and pharmaceutical industries with the most stringent standards.",
    },
  ];
  // const scrollTo = (id: string) => {
  //   const element = document.getElementById(id) as HTMLDivElement;
  //   element.scrollIntoView({
  //     behavior: "auto",
  //   });
  // };
  return (
    <MiddleBlockSection style={{ display: "block" }}>
      <Slide direction="right">
        <h6>Where FIBC bags can be used?</h6>
        <Row justify="center" align="middle">
          <Col lg={24} md={24} sm={24} xs={24}>
            <List
              grid={{
                gutter: 16,
                xs: 1,
                sm: 1,
                md: 3,
                lg: 3,
                xl: 3,
                xxl: 3,
              }}
              dataSource={usecaseList}
              renderItem={(item) => (
                <List.Item>
                  <Card
                    title={
                      <div>
                        <SvgIcon
                          src={item.svg}
                          aria-label="homepage"
                          width="70px"
                          height="70px"
                        />
                        <div>{item.title}</div>
                      </div>
                    }
                    headStyle={{
                      color: "#18216d",
                      fontWeight: "600",
                      height: "100",
                    }}
                    bordered={true}
                    hoverable={true}
                    style={{
                      margin: "3%",
                      borderRadius: "5px",
                      width: "150",
                      textAlign: "center",
                      fontSize: "15px",
                      zIndex: "30",
                      backgroundColor: "white",
                      borderColor: "rgb(24 33 109 / 10%)",
                    }}
                  >
                    {item.des}
                  </Card>
                </List.Item>
              )}
            />
          </Col>
        </Row>
      </Slide>
    </MiddleBlockSection>
  );
};

export default withTranslation()(MiddleBlock);
